import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Avatar from 'material-ui/Avatar'
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import './Head.css'

const styles = {
    bigAvatar: {
        width: '80%',
        height: '80%',
        margin: '10%',
    },
    email: {
        width: '100%',
        fontSize: 15,
        fontFamily: "'Rosario', serif",
    },
    material: {
        width: '100%',
        fontSize: 15,
        fontFamily: "'Rosario', serif",
    },
    link: {
    	textDecoration: 'none',
    	color: '#87CEEB',
    }
}

class Head extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div>
            <Avatar alt='Darren' src={process.env.PUBLIC_URL + '/images/head.jpg'} className={this.props.classes.bigAvatar} />
            <Typography type="body1" component="p" className={this.props.classes.email}>
                <center>
                    darrenyaoyao.huang@gmail.com
                </center>
            </Typography>
            <Typography type="body1" component="p" className={this.props.classes.material}>
                <center>
                    <a href={'https://drive.google.com/file/d/16d9WuXeDnoCP82K7jZJKI0SO9eQi66Yr/view?usp=sharing'} className={this.props.classes.link}>
                        Curriculum Vitae
                    </a>
                </center>
            </Typography>
        </div>
    );
  }
}

Head.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(Head);



